
import Alert from 'vux/src/components/alert/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'

export default {
name: 'Show',
components: {
Alert,
XDialog
},
data () {
return {
now: ''
}
},
created(){
this.getNowTime();
this.go();
},
methods:{

modalbox(){
this.$refs.alertBox.style.display = 'block';
},

toggles(){
this.$refs.alertBox.style.display = 'none';
},

getNowTime(){
var myDate = new Date();
var _minutes = '';

if(myDate.getMinutes() < 10){
_minutes = "0" + myDate.getMinutes();
}else{
_minutes = myDate.getMinutes();
}
var _time = myDate.getHours()+":"+ _minutes;
this.now = _time;
},

go(){
let self = this;
var _i = 1;
var s = setInterval(() => {
console.log(_i);
switch (_i) {
case 1:
self.$refs.go1.style.display = 'block';
break;
case 2:
self.$refs.go2.style.display = 'block';
break;
case 3:
self.$refs.go3.style.display = 'block';
break;
case 4:
self.$refs.go4.style.display = 'block';
break;
case 5:
self.$refs.go5.style.display = 'block';
break;
case 6:
self.$refs.go6.style.display = 'block';
break;
case 7:
self.$refs.go7.style.display = 'block';
break;
case 8:
self.$refs.go8.style.display = 'block';

break;
case 9:
clearInterval(s);
break;
}
_i ++;
var _top = document.documentElement.scrollHeight || document.body.scrollHeight;
document.documentElement.scrollTop =  _top;
document.body.scrollTop =  _top;
},800)

/*document.body.style.overflow = 'hidden';
document.documentElement.style.overflow = 'hidden';*/
}
},
mounted(){

var overscroll = function(el) {
el.addEventListener('touchstart', function() {
var top = el.scrollTop
, totalScroll = el.scrollHeight
, currentScroll = top + el.offsetHeight




if(top === 0) {
el.scrollTop = 1
} else if(currentScroll === totalScroll) {
el.scrollTop = top - 1
}
})
el.addEventListener('touchmove', function(evt) {


if(el.offsetHeight < el.scrollHeight)
evt._isScroller = true
})
}
overscroll(document.getElementById('app'));
/*document.body.addEventListener('touchmove', function(evt) {


if(!evt._isScroller) {
evt.preventDefault()
}
})*/
}
}



